import { useRouter } from 'next/router';
import '@/app/globals.css';
import Header from '@/components/Header';

export default function Shop() {
  const router = useRouter();
  const {shopName} = router.query;
  return (
    <div>
      <Header />
      <section>
        <h1>This is {shopName} shop.</h1>
      </section>
    </div>
  )
}